<template>
  <div class="hello">

      <button @click="tryme" class="button">Try Me</button>

      <p>{{message}}</p>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      message:null
    }
  },
  methods:{
    tryme:function(){
      this.message = Date()
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 { 
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
button{
  background: red;
}
</style>



